.App {
  text-align: center;
}

html {
  background: black;
}

.whiteSmokeText{
  color: whitesmoke;
}

.navBarCtz {
  background: transparent;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@-webkit-keyframes fadeOut {
  0% { opacity: 1;}
  99% { opacity: 0.01;width: 100%; height: 100%;}
  100% { opacity: 0;width: 0; height: 0;}
}
@keyframes fadeOut {
  0% {
    opacity: 0;
  }
  16% {
    opacity: 1;
  }
  84% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}

.loader-container {
  opacity: 0;
  -webkit-animation: fadeOut 2;
  animation: fadeOut 2s;
  animation-fill-mode: forwards;
  width: 100vw;
  height: 100vh;
  position: fixed;
  background: rgba(0, 0, 0, 1)
  url("https://cdn.dribbble.com/users/289158/screenshots/15561847/laser2.gif") center
  no-repeat;
  z-index: 1;
}
